    // props: ['this.data'],
    async created() {
        axios.defaults.headers = {
            'deviceid': 'device_for_web',
        }
        let {
            data
        } = await axios.get('http://118.69.235.218:8080/hotelapi/home/view/findHomePageInfo');
        console.log('1234')
        let data1 = data.detailCollectionList;
        let HotHotel = (data1[1]);
        console.log('1234', HotHotel)
        let dsHotHotel = HotHotel.hotelFormList
        console.log('1234', dsHotHotel)
        this.data = dsHotHotel
        console.log('1234', this.data)
    },
    methods: {
        chooseSpecitalHotel(dataHotel) {
            this.$emit('dsHotel', dataHotel)
            console.log('1234')
            this.$store.dispatch({
                type: 'updateDataRoom',
                data: this.data
            });
            this.$router.push({
                name: 'Category',
                params: {
                    Sn: 0
                }
            })
        },
        openFullScreen1() {
            this.fullscreenLoading = true;
            setTimeout(() => {
                this.fullscreenLoading = false;
            }, 2000);
        },
    }

}
